<template>
  <div>
    <!-- Logo veya dier ierikler -->
    <v-img
        class="mx-auto my-6"
        max-width="228"
        src="/images/logo.svg"
    ></v-img>

    <!-- Kart ierisindeki form -->
    <v-card
        class="mx-auto pa-12 pb-8"
        elevation="8"
        max-width="448"
        rounded="lg"
    >
      <div class="text-subtitle-1 text-medium-emphasis">
        {{ $t("message.email") }}
      </div>

      <v-text-field
          v-model="email"
          :error="hasError"
          density="compact"
          :placeholder="$t('message.enter_email')"
          prepend-inner-icon="mdi-email-outline"
          variant="outlined"
          @keyup.enter="submitForm"
      ></v-text-field>

      <div class="text-subtitle-1 text-medium-emphasis d-flex align-center justify-space-between">
        {{ $t("message.password") }}
      </div>

      <v-text-field
          v-model="password"
          :error="hasError"
          :append-inner-icon="visible ? 'mdi-eye-off' : 'mdi-eye'"
          :type="visible ? 'text' : 'password'"
          density="compact"
          :placeholder="$t('message.enter_password')"
          prepend-inner-icon="mdi-lock-outline"
          variant="outlined"
          @click:append-inner="visible = !visible"
          @keyup.enter="submitForm"
      ></v-text-field>

      <!-- Hata mesaj -->
      <v-alert
          class="mb-5"
          v-if="errorMessage"
          type="error"
          dense
      >
        {{ errorMessage }}
      </v-alert>

      <!-- Bo div: reCAPTCHA widget' burada render edilecek -->
      <div id="myRecaptcha"></div>

      <v-btn
          @click="submitForm"
          :disabled="isSubmitting"
          :loading="isSubmitting"
          class="mb-8 mt-4"
          color="black"
          size="large"
          variant="tonal"
          style="text-transform: none"
          block
          type="submit"
      >
        {{ $t("message.sign_in") }}
      </v-btn>
    </v-card>
  </div>
</template>

<script>
import apiClient from '@/services/apiClient.js';
import { mapActions } from 'vuex';

export default {
  data() {
    return {
      visible: false,
      email: '',
      password: '',
      errorMessage: '',
      hasError: false,
      isSubmitting: false,
      captchaToken: '', // reCAPTCHA token buraya gelecek
    };
  },
  computed: {
    isFormValid() {
      return this.email.trim() !== '' && this.password.trim() !== '';
    },
  },
  methods: {
    ...mapActions(['login']),

    // reCAPTCHA widget' "myRecaptcha" id'li div iinde render ediyoruz
    renderRecaptcha() {
      window.grecaptcha.ready(() => {
        window.grecaptcha.render('myRecaptcha', {
          sitekey: '6LdA7P4qAAAAAD0GgK_zyal2L3jWRi54wplxKUR0', // Kendi site key'iniz
          callback: this.handleCaptchaVerified,          // Token alndnda tetiklenecek fonksiyon
        });
      });
    },

    // Google reCAPTCHA tarafndan retilen token' yakalyoruz
    handleCaptchaVerified(token) {
      this.captchaToken = token;
      console.log('Captcha token:', token);
    },

    // Form gnderme
    submitForm() {
      if (this.isSubmitting) return;

      // reCAPTCHA token yoksa uyar
      if (!this.captchaToken) {
        this.errorMessage = this.$t('message.captcha_required');
        this.hasError = true;
        return;
      }

      // Email ve ifre bo mu kontrol
      if (!this.isFormValid) {
        this.errorMessage = this.$t('message.email_and_password_required');
        this.hasError = true;
        return;
      }

      this.isSubmitting = true;
      this.clearErrors();

      // Backend'e istek gnderiyoruz
      apiClient.post('/login', {
        email: this.email,
        password: this.password,
        captcha_token: this.captchaToken, // Token' backend'e yolluyoruz
      })
          .then(response => {
            // Giri baarlysa Vuex'e token kaydet ve ynlendir
            this.login(response.data.data.access_token, response.data.data.user_id);
            this.$router.push({ name: 'home' });
          })
          .catch(error => {
            console.error('Login error:', error);
            this.errorMessage = this.$t('message.sign_in_error');
            this.hasError = true;
            this.password = '';
          })
          .finally(() => {
            this.isSubmitting = false;
          });
    },

    clearErrors() {
      this.hasError = false;
      this.errorMessage = '';
    },
  },
  mounted() {
    // Eer reCAPTCHA script'i imdiden yklendiyse direkt render et
    if (window.grecaptcha) {
      this.renderRecaptcha();
    } else {
      // Deilse, belli aralklarla window.grecaptcha'nn yklenip yklenmediini kontrol et
      const checkRecaptcha = setInterval(() => {
        if (window.grecaptcha) {
          clearInterval(checkRecaptcha);
          this.renderRecaptcha();
        }
      }, 500);
    }
  },
};
</script>

<style scoped>
#myRecaptcha {
  display: flex;
  justify-content: center;
  margin: 16px 0;
}
</style>
